.search-bars-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.icon-info {
  align-self: flex-end;
  padding-right: 40px;
  max-width: 20px;
  max-height: 20px;
  cursor: help;
  animation: wiggle 10s linear infinite;
}

.search-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-right: 10px;
  margin-top: 10px;
  width: 80%;
  height: 35px;
  border-radius: 40px;
  border: 1.5px #264653 solid;
  background-color: white;
}

.search-icon {
  max-width: 20px;
  max-height: 20px;
}

.genre-search-bar,
.title-search-bar,
.author-search-bar {
  width: 80%;
  border: 0px transparent;
}

.search-buttons-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  padding-top: 20px;
}

.search-button {
  width: 30%;
  height: 30px;
  background-color: #2a9d8f;
  color: white;
  border-radius: 20px;
  border: 1.5px #264653 solid;
  margin-right: 5px;
  cursor: pointer;
}

.advanced-search-button {
  width: 70%;
  height: 30px;
  background-color: #264653;
  color: white;
  border-radius: 20px;
  border: 1.5px #264653 solid;
  margin-left: 5px;
  cursor: pointer;
}

.advanced-search-input-container {
  padding-top: 5px;
  width: 100%;
  display: none;
}

.advanced-search-input-container.active {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}

/* Mobile */
@media only screen and (max-width: 450px) {
}

/* Tablet Styles */
@media only screen and (min-width: 451px) and (max-width: 960px) {
  .search-bars-container {
    width: 70%;
  }

  .icon-info {
    padding-right: 55px;
  }

  .search-buttons-container {
    justify-content: space-around;
  }

  .search-button {
    width: 35%;
    height: 30px;
    margin-right: 10px;
  }

  .advanced-search-button {
    width: 50%;
    height: 30px;
    margin-left: 10px;
  }
}

/* Desktop Styles */
@media only screen and (min-width: 961px) {
  .search-bars-container {
    width: 50%;
  }

  .icon-info {
    padding-right: 55px;
  }

  .search-buttons-container {
    justify-content: space-around;
  }

  .search-button {
    width: 40%;
    height: 30px;
    margin-right: 10px;
  }

  .advanced-search-button {
    width: 50%;
    height: 30px;
    margin-left: 10px;
  }
}

@media only screen and (min-width: 1440px) {
  .search-bars-container {
    width: 40%;
  }

  .icon-info {
    padding-right: 65px;
  }

  .search-buttons-container {
    justify-content: center;
  }

  .search-button {
    width: 25%;
    height: 30px;
    margin-right: 10px;
  }

  .advanced-search-button {
    width: 38%;
    height: 30px;
    margin-left: 10px;
  }
}

/* 4K */
@media only screen and (min-width: 2560px) {
  .search-bars-container {
    width: 25%;
    margin-top: 50;
  }

  .icon-info {
    padding-right: 65px;
  }

  .search-button {
    width: 35%;
    height: 30px;
  }

  .advanced-search-button {
    width: 45%;
    height: 30px;
  }
}

/* Keyframes */
@keyframes wiggle {
  0%,
  7% {
    transform: rotateZ(0);
  }
  15% {
    transform: rotateZ(-15deg);
  }
  20% {
    transform: rotateZ(10deg);
  }
  25% {
    transform: rotateZ(-10deg);
  }
  30% {
    transform: rotateZ(6deg);
  }
  35% {
    transform: rotateZ(-4deg);
  }
  40%,
  100% {
    transform: rotateZ(0);
  }
}
